import React, { useState } from "react";
import { FiChevronDown, FiChevronUp } from "react-icons/fi"; // React Icons kullanarak ok ikonlarn ekledik
import "../../styles/Sss.css"; // CSS dosyasn import ediyoruz

const Sss = () => {
  // Her bir soru iin 'isOpen' durumunu tutan bir state
  const [openIndex, setOpenIndex] = useState(null);

  // Sorularn ve cevaplarnn verileri
  const faqs = [
    {
      question: "1. Online veteriner hizmeti nasl alr?",
      answer:
        "Platformumuz zerinden veterinerlerle grntl veya sesli grme yaparak evcil hayvannz iin danmanlk alabilirsiniz.",
    },
    {
      question: "2. Online grmeler cretli mi?",
      answer:
        "Evet, her veteriner kendi cret tarifesini belirler. Grme ncesinde cret bilgilerini grebilirsiniz.",
    },
    {
      question: "3. Veterinerlerle nasl iletiime geebilirim?",
      answer:
        "Sitemizdeki Veterinerler blmnden uzmanlar inceleyerek uygun olan birini seebilir ve randevu alabilirsiniz.",
    },
    {
      question: "4. Evcil hayvanmn a takvimini takip edebilir miyim?",
      answer:
        "Evet! A ve muayene hatrlatc hizmetimiz ile evcil hayvannzn salk programn takip edebilirsiniz.",
    },
    {
      question: "5. Hesabm nasl oluturabilirim?",
      answer:
        "Anasayfadaki Kayt Ol butonuna tklayarak e-posta adresinizle hzlca ye olabilirsiniz.",
    },
  ];

  // Soruya tklandnda cevab ama / kapama
  const toggleAnswer = (index) => {
    if (openIndex === index) {
      setOpenIndex(null); // Eer ayn soruya tekrar tklanrsa, cevab kapat
    } else {
      setOpenIndex(index); // Cevap alr
    }
  };

  return (
    <section id="sss" className="sss-container">
      <h2 className="sss-title">Ska Sorulan Sorular</h2>
      {faqs.map((faq, index) => (
        <div key={index} className="sss-item">
          <div className="sss-header" onClick={() => toggleAnswer(index)}>
            <h3 className="sss-question">{faq.question}</h3>
            <div className="sss-toggle-icon">
              {openIndex === index ? (
                <FiChevronUp size={20} color="#e11d48" />
              ) : (
                <FiChevronDown size={20} color="#e11d48" />
              )}
            </div>
          </div>
          {openIndex === index && <p className="sss-answer">{faq.answer}</p>}
        </div>
      ))}
    </section>
  );
};

export default Sss;
